@import "../general/mixins.scss";
@import "../general/variables.scss";

.video__heading {
  text-align: center;
}

.video__player {
  text-align: center;
}

.video__player-splash-screen {
  object-fit: cover;
  margin-bottom: -67px;

  @media (min-width: $tablet-width) {
    width: 648px;
    height: 500px;
    margin-bottom: -20px;
  }

  @media (min-width: $desktop-width) {
    width: 800px;
    height: 500px;
    margin-bottom: 22px;
  }
}

.video__play-button {
  position: relative;
  top: -50px;
  left: 136px;
  fill: rgba(255, 255, 255, 0.7);

  @media (min-width: $tablet-width) {
    top: -262px;
    left: 360px;
  }

  @media (min-width: $desktop-width) {
    top: -307px;
    left: 575px;
  }
}
